
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.display-enter-active, .display-leave-active{
  transition: all .3s ease;
}
.display-enter, .display-leave-to {
  opacity: 0;
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}
.container{
  height: 100%;
}
.dropdown .active{
  background-color: #e78170;
}
.dropdown .active a{
  color: #ffffff;
}
@media screen and (max-width: 1100px){
.home{
    width: calc(100% - 80px);
    margin-right: 0;
}
}
@media screen and (max-width: 1300px){
.home{
    width: calc(100% - 240px);
    margin-left: 40px;
}
.nav-user li{
      width: 40px;
      height: 30px;
      overflow: hidden;
}
.sidebar{
    transition: all 0.5s;
    width: 40px;
    overflow: hidden;
}
.sidebar li{
    padding: 20px 0;
}
.sidebar li a{
    padding: 0 auto;
    margin:0 auto;
}
.sidebar i{
    font-size: 30px;
}
.sidebar .fa-mobile{
    margin-right: 8px;
    font-size: 50px;
}
}



