
.btn-group{
	text-align: left;
	margin-left: 18px;
	margin-top:20px;
}
.btn-group li{
	margin: 5px 5px;
}
.btn-group .active{
	background-color: #de533a;
}
.btn-group .active a{
	color: #ffffff;
}
.btn-group li{
	margin: 0 5px;
	display: inline-block;
	padding: 5px 10px;
	border: 1px solid #d9d9d9;
	border-radius: 10px;
	cursor: pointer;
}



