
.list{
    	margin-left: 30px;
    	margin-top: 17px;
   	 	margin-bottom: 17px;
   	 	padding-bottom: 17px;
   		width: calc(100% - 30px);
    	border-bottom:1px dashed #efefef;
		border-bottom: 1px dashed #d9d9d9;
}
.list-top{
		padding-top: 10px; 
		text-align: left;
}
.small-text-group {
		text-align: left;
}
.list .title {
		text-align: left;
}
.list .title a{
		margin-top: 10px;
    	margin-bottom: 10px;
    	margin-left: 0;
    	display: inherit;
    	font-size: 18px;
    	font-weight: bold;
    	line-height: 1.5;
}



